.search-row
    display: flex
    justify-content: space-between
    flex-wrap: wrap
    width: 100%
    // height: 60px
    padding: 10px 10px 20px 10px
    &__btn
        margin-left: 10px
        +max-screen(400px)
            width: 100%
            margin-top: auto
            margin-left: 0
        .btn__min-yellow
            height: 33px
    +max-screen(400px)
        // height: 100px
    &__sorting
        color: $text-color

.search
    display: flex
    justify-content: space-between
    height: 33px
    border: 2px solid $color-blue
    align-items: center
    border-radius: 4px
    width: 100%
    +max-screen(400px)
        width: 100%
        margin-bottom: 10px
   
    &.btn-in-search
        width: calc( 100% - 160px )
        +max-screen(400px)
            width: 100%
            margin-bottom: 10px
       
    &.sorting-in-search
        width: 50%
        +max-screen(400px)
            width: 100%
            margin-bottom: 10px

    &:hover
        border: 2px solid $color-yellow
        .search__button
            background-color: $color-yellow
        .search__img
            color: $color-yellow
    &__label
        display: flex
        align-items: center
        height: 100%
    &__img
        margin-left: 3px
        color: $color-blue
        height: 20px
        svg
            width: 20px
            height: 20px
    &__input
        color: $text-color
        width: 100%
        height: 100%
        padding-left: 10px
    &__button
        display: flex
        align-items: center
        background-color: $color-blue
        height: 100%
        svg
            width: 20px
            color: white

