.carusel
    display: flex
    width: 100%
    padding: 20px 40px
    flex-wrap: wrap
    &__wrap
        width: calc(100% - 60px + 10px)
        margin: -5px
        overflow: hidden
    &__list
        display: flex
        justify-content: center
        align-items: center
        flex-wrap: wrap
        flex-direction: column
        justify-content: flex-start
        align-items: flex-start
        height: auto
        transition: 0.3s
        +max-screen(740px)
            width: calc(100% + 5px)
        +max-screen(440px)
            width: calc(100% + 5px)
    &__item
        display: flex
        align-items: center
        justify-content: center
        border: 1px solid $border-color
        // min-height: 154px
        width: calc(25% - 10px)
        border-radius: 4px
        position: relative
        margin: 5px
        &:hover
            border: 1px solid $color-yellow
        +max-screen(740px)
            width: calc(50% - 10px)
        +max-screen(440px)
            width: calc(100% - 10px)
