.content-title
    display: flex
    justify-content: center
    flex-direction: column
    height: 80px
    padding: 10px 0 10px 10px
    border-bottom: 1px solid $border-color
    color: $text-color    
    +max-screen(780px)
        height: 120px
    &.adm
        +max-screen(860px)
            height: 120px
    &.no-sub-menu
        padding-left: 210px
        +max-screen(780px)
            height: 80px
            padding-left: 10px
        &.adm
            +max-screen(860px)
                height: 80px
                padding-left: 10px

    &__title
        width: 100%
        font-weight: bold
    &__crumbs
        color: $text-color
        margin-bottom: 5px
        // img
        //     width: 10px
        //     margin-top: auto
    &__link
        font-size: 0.85em
        &:hover
            color: $color-yellow
    &__img
        width: 8px
        height: 8px
    &__text
        font-size: 0.85em


