.list
    display: flex
    flex-direction: column
    width: 100%
    &__item
        display: flex
        justify-content: space-between
        border: 1px solid $border-color
        margin: 0px 10px 10px
        padding: 5px
        border-radius: 4px
        color: $text-color
        width: calc( 100% - 20px )
        font-size: 1em
        &:hover
            border: 1px solid $color-yellow
        +max-screen(880px)
            font-size: 0.85em

    &__description
        display: flex
        flex-direction: column
        justify-content: center
    &__id
        display: flex
        flex-wrap: wrap
        .id
            opacity: 0.7
            font-size: 0.85em
        .name
            width: 100%
            padding-right: 5px

    &__cost
        display: flex
        align-items: center
        min-width: 100px
        font-size: 1em
        // margin-left: auto
        text-align: left
        white-space: nowrap
        +max-screen(880px)
            font-size: 0.85em
            min-width: 70px
    &__client
        display: flex
        flex-wrap: wrap
        width: 100%
        .id, .last-visit
            width: 100%
            opacity: 0.7
            font-size: 0.85em
        .name
            width: calc( 100% / 3 )
        .tel
            width: calc( 100% / 3 )
        .pets
            width: calc( 100% / 3 )

