.btn__max-yellow, .btn__min-yellow
    display: flex
    align-items: center
    justify-content: center
    width: auto
    min-width: 150px
    height: 30px
    padding: 0 10px 
    border: 1px solid $color-yellow
    border-radius: 4px
    background-color: $color-yellow
    color: #FFFFFF
    white-space: pre
    &:hover
        background-color: white
        border: 1px solid $color-yellow
        color: $color-yellow
        
.btn__max-yellow
    font-size: 1em
.btn__min-yellow
    font-size: 0.85em

